.spinner {
  align-items: center;
  display: flex;
  height: 80%;
  justify-content: center;
}

.blocks {
  display: flex;
}

.block {

  --sz: 4.5vmin;

  --tX: 0;

  --animation: 700ms cubic-bezier(0.3, 0.5, 0.4, 0.9) infinite alternate-reverse;

  --hm: 4.5vmin;
  background-image: var(--bg);
  border-radius: 50%;
  height: var(--sz);
  mix-blend-mode: lighten;
  transform: translateX(var(--tX));
  width: var(--sz);
}

.white {

  --bg: linear-gradient(-50deg, white 0%, #d1d1d1 50%);
  animation: attract-white var(--animation);
  margin-right: var(--hm);
}

.green {

  --bg: linear-gradient(50deg, #00ba5e 0%, #b9ffdc 50%);
  animation: attract-green var(--animation);
  margin-left: var(--hm);
}

@keyframes attract-white {

  to {
    transform: translateX(calc(var(--sz) + calc(var(--hm) / 4)));
  }
}

@keyframes attract-green {

  to {
    transform: translateX(calc(var(--sz) * -1 - calc(var(--hm) / 4)));
  }
}
